import { Modal, Button } from "react-bootstrap";

const ModalDialog = ({ message, clrMsg }) => {
  return (
    <Modal show={message !== "" ? true : false} centered={true}>
      <Modal.Header
        closeButton
        onClick={() => {
          clrMsg("");
        }}
      >
        <Modal.Title>Error !!</Modal.Title>
      </Modal.Header>

      <Modal.Body className="text-center" style={{ color: "red" }}>
        <p>{message}.</p>
      </Modal.Body>

      <Modal.Footer>
        <Button
          variant="primary"
          onClick={() => {
            clrMsg("");
          }}
        >
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ModalDialog;
