import "../pages/css/login.css";
import { useContext, useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { authinticate } from "../helpers/api";
import { TextField, Button } from "@mui/material";
import Cookies from "js-cookie";
import { AppContext } from "../helpers/Context";
import { SendOutlined } from "@mui/icons-material";
/*eslint-disable */

const Login = () => {
  const navigate = useNavigate();
  const { state } = useLocation();
  const { appData, setAppData } = useContext(AppContext);

  let [inputs, setInputs] = useState({ email: "", password: "" });
  let [isEmail, setIsEmail] = useState(false);
  let [errorMsg, setErrMsg] = useState("");

  const handleChange = (e) => {
    var pattern = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    setInputs({ ...inputs, email: e.target.value });

    setIsEmail(inputs.email.match(pattern));
  };

  const handleLogin = (e) => {
    e.preventDefault();
    let credentials = {};
    credentials.email = inputs.email;
    credentials.password = inputs.password;

    authinticate(credentials).then((resp) => {
      setAppData({ ...appData, authed: true });
      localStorage.setItem("userInfo", JSON.stringify(resp.data));
      Cookies.set("Authed", JSON.stringify(resp.data));
      navigate("/messages");
    });
  };

  useEffect(() => {
    if (appData.authed) {
      navigate(state?.path || "/messages");
    }
  }, []);

  return (
    <div className="login-container">
      <div className="login">
        <div className="text-center header">
          <h1>jainaSOFT</h1>
          <p>{errorMsg}</p>
        </div>
        <form action="">
          <div className="rows form-group">
            <TextField
              type="text"
              className="form-control"
              style={{ color: isEmail ? "black" : "red" }}
              name="email"
              label="Email Address"
              onChange={handleChange}
              onBlur={handleChange}
            />
          </div>
          <div className="rows form-group">
            <div>
              <TextField
                type="password"
                className="form-control"
                name="password"
                label="Passoword"
                onChange={(e) => {
                  setInputs({ ...inputs, password: e.target.value });
                }}
              />
            </div>
          </div>
          <div className="rows text-center">
            <Button
              endIcon={<SendOutlined />}
              type="button"
              variant="contained"
              color="primary"
              size="large"
              onClick={handleLogin}
              disabled={!isEmail ? true : false}
            >
              Send
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
