import { Row, Col } from "react-bootstrap";
import { Button } from "@mui/material";
import {} from "@mui/icons-material";
import "./GLJournal.css";
import { convertToCurrency } from "../../helpers/libs";
import { MdDeleteForever } from "react-icons/md";
import { useContext } from "react";
import { JContext } from "../../helpers/Context";

const GLjournal = ({ entries, setEntries }) => {
  const { jdata, setJdata } = useContext(JContext);
  const delEntry = (i) => {
    const _sum = { ...jdata };
    setJdata({
      ..._sum,
      dr: _sum.dr - entries[i].debit,
      cr: _sum.cr - entries[i].credit,
    });

    const _entries = [...entries];
    _entries.splice(i, 1);
    setEntries(_entries);
  };
  return (
    <>
      {entries.map((entry, index) => {
        return (
          <Row className="row-brs" key={index}>
            <Col xs lg="1" className="col-border">
              {index + 1}
            </Col>
            <Col xs lg="2" className="col-border">
              {entry.gl_account}
            </Col>
            <Col xs lg="4" className="col-border">
              {entry.gl_name}
            </Col>
            <Col xs lg="2" className="col-border text-end">
              {convertToCurrency(entry.debit)}
            </Col>
            <Col xs lg="2" className="col-border text-end">
              {convertToCurrency(entry.credit)}
            </Col>
            <Col xs lg="1" className="col-border">
              <Button
                size="lg"
                fullWidth={true}
                variant="contained"
                color="primary"
                onClick={() => {
                  delEntry(index);
                }}
              >
                <MdDeleteForever />
              </Button>
            </Col>
          </Row>
        );
      })}
    </>
  );
};

export default GLjournal;
