import axios from "axios";
import Cookies from "js-cookie";

axios.defaults.withCredentials = true;
axios.defaults.baseURL = "http://localhost:8000/";

const headers = {
  Accept: "application/json",
  "Content-Type": "application/json",
  Authorization: "",
};

axios.defaults.headers.common = headers;

const authinticate = (credentials) => {
  return new Promise((resolve, reject) => {
    axios
      .get("sanctum/csrf-cookie")
      .then((resp) => {
        axios.post("api/login", credentials).then((resp) => {
          localStorage.setItem("userInfo", JSON.stringify(resp.data));
          Cookies.set("Authed", JSON.stringify(resp.data));
          resolve(resp);
        });
      })
      .catch((error) => {
        console.log(error);
        reject(error);
      });
  });
};

const getGljournals = () => {
  headers.Authorization = getToken();
  return new Promise((resolve, reject) => {
    axios
      .get("api/journal", null)
      .then((resp) => {
        resolve(resp.data[0].ChartOfAc);
      })
      .catch((err) => {
        reject(err);
      });
  });
};

const getToken = () => {
  if (localStorage.getItem("userInfo")) {
    const store = JSON.parse(localStorage.getItem("userInfo"));
    return `Bearer ${store.token}`;
  }
  return false;
};

const glPost = (payload) => {
  console.log(payload);
  headers.Authorization = getToken();
  return new Promise((resolve, reject) => {
    axios.post("/api/journal", payload).then((resp) => {
      console.log(resp);
      resolve(resp);
    });
  });
};

export { authinticate, getGljournals, glPost };
