import Cookies from "js-cookie";
const Logout = () => {
  const logOut = (e) => {
    e.preventDefault();
    Cookies.remove("Authed");
  };
  return (
    <div>
      <button
        type="submit"
        name="logout"
        className="btn btn-danger"
        onClick={logOut}
      >
        LOGOUT
      </button>
    </div>
  );
};

export default Logout;
