import { Container, Col, Row, FloatingLabel, Form } from "react-bootstrap";
import { Button } from "@mui/material";
import { useEffect, useState, useRef, useContext } from "react";
import { getGljournals } from "../../helpers/api";
import GLJournal from "./GLJournal";
import "./GLEntry.css";
import JournalLayout from "./JournalLayout";
import { JContext } from "../../helpers/Context";
import { FileDownload } from "@mui/icons-material";
import {
  Autocomplete,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import ModalDialog from "../../components/ModalDialog";
import { PropagateLoader } from "react-spinners";
import { todayISO } from "../../helpers/libs";

const GLEntry = () => {
  // eslint-disable-next-line
  const [journals, setjournals] = useState([]);
  const refAmt = useRef(0);
  const gl_account = useRef("");
  const gl_date = useRef("2021-12-31");
  const [entries, setEntries] = useState([]);
  const [dateState, setDateState] = useState(false);
  const { jdata, setJdata } = useContext(JContext);
  const [val, setVal] = useState({});
  const [gl_name, SetGLName] = useState("---");
  const [msg, setMsg] = useState("");
  const [btnState, setbtnState] = useState(true);
  const [trnType, setTrnType] = useState("debit");
  const [isTrue, setIsTrue] = useState({ shrink: false });

  //setJdata({ ...jdata, date: todayISO() });

  const getCode = (e) => {
    var found = false;
    if (e.target.value !== "") {
      journals.forEach((o, index) => {
        if (o.gl_account === e.target.value && o.gl_account.length > 1) {
          //setHasValue(true);
          setVal(journals[index]);
          SetGLName(o.gl_name);
          found = true;
        }
      });
      if (!found) {
        setVal(journals[0]);
      }
    } else {
    }
  };

  const currentType = (e) => {
    setTrnType(e.target.value);
  };

  const selectAccount = (event, data) => {
    if (data !== null) {
      if (data.gl_account !== "---") {
        setIsTrue({ shrink: true });
        gl_account.current.value = data.gl_account;
        SetGLName(data.gl_name);
        setVal(data);
      } else {
        gl_account.current.value = "";
      }
    }
    if (gl_account !== "---" && parseFloat(refAmt.current.value) > 0) {
      setbtnState(false);
    } else {
      setbtnState(true);
    }
  };

  const checkData = () => {
    if (val.gl_name !== undefined && parseFloat(refAmt.current.value) > 0) {
      setbtnState(false);
    } else {
      setbtnState(true);
    }
  };

  const handleAdd = (e) => {
    e.preventDefault();
    if (parseFloat(refAmt.current.value) === 0 || gl_name === "---") {
      setMsg("Account and Amount is required");
      return;
    }
    setDateState(entries.length > 0 ? false : true);
    const debit = trnType === "debit" ? refAmt.current.value : 0;
    const credit = trnType === "credit" ? refAmt.current.value : 0;

    const entry = {
      gl_account: gl_account.current.value,
      gl_name: gl_name,
      debit: parseFloat(debit),
      credit: parseFloat(credit),
    };
    setEntries([...entries, entry]);

    setJdata({
      ...jdata,
      dr: parseFloat(jdata.dr) + parseFloat(debit),
      cr: parseFloat(jdata.cr) + parseFloat(credit),
      date: gl_date.current.value,
    });
  };

  useEffect(() => {
    getGljournals().then((ChartOfAc) => {
      const head = [
        {
          gl_account: "---",
          gl_name: "Select Account",
          gl_group: "",
        },
      ];
      setjournals([...head, ...ChartOfAc]);
      gl_date.current.value = todayISO();
    });
  }, []);

  return (
    <>
      <ModalDialog message={msg} clrMsg={setMsg} />
      <Container
        style={{
          padding: "20px",
          border: "1px solid silver",
          background: "azure",
        }}
        className="mt-3"
      >
        <Row className="g-0">
          <Col>
            <FloatingLabel controlId="floatingInput" label="Date">
              <Form.Control
                ref={gl_date}
                type="date"
                placeholder="Date"
                onChange={() => {
                  setJdata({ ...jdata, date: gl_date.current.value });
                }}
                disabled={dateState}
              />
            </FloatingLabel>
          </Col>
          <Col xs lg="2">
            <FormControl fullWidth>
              <InputLabel id="select-label">TR Type</InputLabel>
              <Select id="simple-select" value={trnType} onChange={currentType}>
                <MenuItem value={"debit"}>Debit</MenuItem>
                <MenuItem value={"credit"}>Credit</MenuItem>
              </Select>
            </FormControl>
          </Col>
          <Col xs lg="2">
            <TextField
              inputRef={gl_account}
              type="number"
              label="GL-Code"
              InputLabelProps={isTrue}
              onChange={getCode}
            />
          </Col>
          <Col xs lg="3">
            <Autocomplete
              value={val}
              options={journals}
              onChange={selectAccount}
              getOptionLabel={(option) => option.gl_name || ""}
              isOptionEqualToValue={(option, value) => option.id === value.id}
              renderInput={(params) => (
                <TextField {...params} label="Select Account" />
              )}
            />
          </Col>
          <Col xs lg="2">
            <TextField
              inputRef={refAmt}
              type="number"
              defaultValue={0}
              onChange={checkData}
              label="Amount"
              inputProps={{ style: { textAlign: "right" } }}
            />
          </Col>
          <Col xs lg="1">
            <Button
              disabled={btnState}
              sx={{
                paddingTop: "14px",
                paddingBottom: "14px",
              }}
              fullWidth={true}
              size="lg"
              variant="contained"
              color="primary"
              onClick={handleAdd}
            >
              <FileDownload />
            </Button>
          </Col>
        </Row>
      </Container>
      <Container>
        <Row>
          <Col lg="12" className="mt-1 text-center">
            <PropagateLoader loading={jdata.postInprogress} />
          </Col>
        </Row>
      </Container>

      <Container className="mt-1 journal-container">
        <JournalLayout>
          <GLJournal entries={entries} setEntries={setEntries} />
        </JournalLayout>
      </Container>
    </>
  );
};

export default GLEntry;
