import { JContext } from "../../helpers/Context";
import { useState } from "react";
import GLEntry from "./GLEntry";

const GLTrans = () => {
  const [jdata, setJdata] = useState({
    dr: 0,
    cr: 0,
    dialog: false,
    entries: [],
    date: null,
    postInprogress: false,
  });
  return (
    <JContext.Provider value={{ jdata, setJdata }}>
      <GLEntry />
    </JContext.Provider>
  );
};

export default GLTrans;
