import { Row, Col } from "react-bootstrap";
import { convertToCurrency } from "../../helpers/libs";
import { JContext } from "../../helpers/Context";
import { useContext, useRef } from "react";
import { glPost } from "../../helpers/api";
import { CloudUpload } from "@mui/icons-material";

import { Button, TextField } from "@mui/material";

const FileUpload = ({ entries }) => {
  // eslint-disable-next-line
  const { jdata, setJdata } = useContext(JContext);
  const isEqual = jdata.dr === jdata.cr;
  const check = useRef("");
  const postEntry = () => {
    setJdata({ ...jdata, postInprogress: true });
    const payload = {
      trans: entries,
      trDate: jdata.date,
    };
    glPost(payload).then((resp) => {
      setJdata({ ...jdata, postInprogress: false });
    });
  };
  return (
    <Row className="row-brs" style={{ background: "azure" }}>
      <Col lg="5" className="col-border">
        <Button
          size="lg"
          variant="contained"
          color="primary"
          startIcon={<CloudUpload />}
          style={{ width: "100%", paddingTop: "15px", paddingBottom: "15px" }}
        >
          Upload Files
        </Button>
      </Col>
      <Col lg="2" className="col-border">
        <TextField label="Check Number" ref={check} />
      </Col>

      <Col
        className="col-border fw-bold"
        lg="2"
        style={isEqual ? { color: "green" } : { color: "red" }}
      >
        <TextField
          label="Debit"
          style={{ textAlign: "right" }}
          value={convertToCurrency(jdata.dr)}
          inputProps={{ readOnly: true, style: { textAlign: "right" } }}
        />
        {/* {convertToCurrency(jdata.dr)} */}
      </Col>
      <Col
        lg="2"
        className="col-border fw-bold"
        style={isEqual ? { color: "green" } : { color: "red" }}
      >
        <TextField
          label="Credit"
          style={{ textAlign: "right" }}
          value={convertToCurrency(jdata.cr)}
          inputProps={{ readOnly: true, style: { textAlign: "right" } }}
        />
        {/* {convertToCurrency(jdata.cr)} */}
      </Col>
      <Col
        xs
        lg="1"
        className="col-border"
        style={{ borderRight: "none", paddingTop: "10px" }}
      >
        <Button
          onClick={postEntry}
          disabled={
            jdata.dr !== jdata.cr ||
            parseFloat(jdata.dr) === 0 ||
            parseFloat(jdata.cr) === 0
              ? true
              : false
          }
          size="lg"
          title="Save"
          variant="contained"
          color="primary"
          sx={{ width: "100%", paddingTop: "14px", paddingBottom: "14px" }}
        >
          <CloudUpload />
        </Button>
      </Col>
    </Row>
  );
};

export default FileUpload;
